div.scroll-container {
    height: 100vh;
    width: 100%;
    overflow: hidden;
    position: relative;
}
div.scroll-container .scroll {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    overflow-y: scroll;
}
div.scroll-container .scroll-1 {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 35px;
    overflow-y: scroll;
}
.title {
    display: inline-block;
    min-height: 500px;
    min-width: 1%;
    position: fixed;
    top: 10%;
    right: 48%;

    /* border: 3px solid #73AD21; */
}
.flex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    margin-top: 3.5%;
}
.img-animation{
    -webkit-animation: 1.1s cubic-bezier(0, 0.03, 0.15, 1.6) 0s 1 slideInFromTop;
            animation: 1.1s cubic-bezier(0, 0.03, 0.15, 1.6) 0s 1 slideInFromTop;
}
.img {
    margin-top: 20px;
}
.info-animation {
    -webkit-animation: 1.1s cubic-bezier(0, 0.03, 0.15, 1.6) 0s 1 slideInFromBottom;
            animation: 1.1s cubic-bezier(0, 0.03, 0.15, 1.6) 0s 1 slideInFromBottom;
}

